import React from 'react';
import decodedTextAreaProps from '../models/decodedTextAreaProps';

const DecodedTextArea: React.FC<decodedTextAreaProps> = ({ text, encoding }) => {
  if (text !== "") {
    return (
      <div>
        <h2></h2>
        <div>
          <p>{text}</p>
        </div>
        <p>{encoding}</p>
      </div>
    )
  } else {
    return null;
  }
}

export default DecodedTextArea