import React from 'react'
import { useState, useRef, useEffect } from 'react';
import TodoList from './components/TodoList';
import { Button, TextField } from '@mui/material';
import Todo from './models/todo';

const TodoApp: React.FC = () => {

  const [todos, setTodos] = useState<Array<Todo>>([]);

  const todoNameRef = useRef<HTMLInputElement>(null);

  // useEffectLocalStoragetodo
  useEffect(() => {
    const storedTodos = JSON.parse(
      localStorage.getItem('todos') || '[]'
    ).map((todoObj: any) => new Todo(todoObj));
    setTodos(storedTodos);
  }, []);

  // useEffecttodoLocalStoragetodo
  useEffect(() => {
    localStorage.setItem('todos', JSON.stringify(todos));
  }, [todos]);

  const handleAddTodo = (e: React.MouseEvent<HTMLButtonElement>) => {
    // 
    const name = todoNameRef.current!.value;
    if (name === "") return;
    setTodos((prevTodos) => {
      return [...prevTodos, new Todo({name: name})];
    });
    todoNameRef.current!.value = "";
  };

  const toggleTodo = (id: string) => {
    const newTodos = [...todos];
    const todo = newTodos.find((todo) => todo.id === id);
    if (todo !== undefined) {
      todo.toggleTodo();
    }
    setTodos(newTodos);
  };

  const handleDeleteCompletedTodo = (e: React.MouseEvent<HTMLButtonElement>) => {
    const newTodos = [...todos].filter((todo) => !todo.completed);
    setTodos(newTodos);
  };

  return (
    <div className="todo-app">
      <h1>Todo List</h1>
      <TodoList todos={todos} toggleTodo={toggleTodo}/>
      <div>:{todos.filter((todo) => !todo.completed).length}</div>
      <div className="todo-name-input-container">
        <TextField
          label=""
          variant="outlined"
          inputRef={todoNameRef}
          fullWidth
          placeholder=""
        />
      </div>
      <div className="action-buttons">
        <Button variant="contained" onClick={handleAddTodo}></Button>
        <Button variant="contained" onClick={handleDeleteCompletedTodo}></Button>
      </div>
      <h2>
        
      </h2>
      <p>
        
      </p>
      <p>
        Todo
      </p>
    </div>
  )
}

export default TodoApp;
