import React, { useState } from 'react';
import Todo from '../models/todo';
import TodoListItem from './TodoListItem';
import { MenuItem, Select, SelectChangeEvent } from '@mui/material';

type todoListProps = {
  todos: Array<Todo>,
  toggleTodo: (id: string) => void,
}

enum SortType {
  Asc = "asc",
  Desc = "desc",
}

const TodoList: React.FC<todoListProps> = ({todos, toggleTodo}) => {
  const [sortBy, setSortBy] = useState<SortType>(SortType.Asc);

  // Todo
  const sortTodos = (todos: Array<Todo>, sortType: SortType) => {
    const sortedTodos = [...todos].sort((a, b) => {
      const nameA = a.name.toLowerCase();
      const nameB = b.name.toLowerCase();
      if (nameA < nameB) {
        return sortType === SortType.Asc ? -1 : 1;
      }
      if (nameA > nameB) {
        return sortType === SortType.Asc ? 1 : -1;
      }
      return 0;
    });
    return sortedTodos;
  };

  // Todo
  const sortedTodos = sortTodos(todos, sortBy);

  // Todo
  const handleSortChange = (event: SelectChangeEvent) => {
    setSortBy(event.target.value as SortType);
  };

  return (
    <div>
      
      <Select value={sortBy} onChange={handleSortChange}>
        <MenuItem value={SortType.Asc}>A-Z</MenuItem>
        <MenuItem value={SortType.Desc}>Z-A</MenuItem>
      </Select>
      <ul>
        {sortedTodos.map((todo) =>
          <li key={todo.id}>
            <TodoListItem todo={todo} toggleTodo={toggleTodo}></TodoListItem>
          </li>
        )}
      </ul>
    </div>
  );
};

export default TodoList;
