const fixedVariables = ['x', 'y', 'z', 'w'];


/**
 * 
 * 5x_{1}, x_{2}, x_{3}, ... 
 * @param length 
 */
export const generateVariables = (length: number): string[] => {
  if (length <= fixedVariables.length) {
    return fixedVariables.slice(0, length);
  }
  return Array.from({ length: length }, (_, i) => `x_{${i + 1}}`);
}
